import { Component, OnInit } from '@angular/core';
import { SkillCardComponent } from './components/skill-card.component';
import { SkillBoxComponent } from './components/skill-box.component';
import { ThirdPartyBoxComponent } from './components/third-party-box.component';

import { Skill } from './skill';

@Component({
  selector: 'app-skills',
  templateUrl: './skills.component.html',
  styleUrls: ['./skills.component.css']
})
export class SkillsComponent implements OnInit {

  skillDescription: string;
  skills: Array<Skill> = [
    new Skill('Ruby', "Because of how often I'm working in projects with Rails, I use Ruby quite often. Ruby was one of the first object oriented languages I learned. The syntax is very intuitive as there are many powerful built in methods and classes. Ruby will remain one of the core languages I practice for years to come as I continue to use Rails moving forward.", 'ruby.png'),
    new Skill('JavaScript', "The very first interpreted language I chose to learn was JavaScript. I had experimented with HTML and CSS but wanted to add functionality and interactivity to my pages. JavaScript has become one of my more comfortable languages. I love how adaptable it is, and how many frameworks use it. At first I would use JavaScript in conjunction with jQuery, but these days I'm using js with frameworks like AngularJS or ReactJS.", 'js.png'),
    new Skill('Ruby on Rails', "My current software engineering position has many projects using Ruby on Rails for APIs or for the full MVC pattern. In both of my previous positions, we used Rails as the backend framework. I'm comfortable with the MVC structure, configuring, and deploying Rails apps. With the support for Rails out there, and the amount of Ruby gems it is easy to apply Rails to many different applications. Deploying Rails apps to AWS or Heroku environments is fairly common to me now. ", 'rails.png'),
    new Skill('Node.js',"Node is something that I often use for smaller microservice type projects or a project using the M.E.A.N. stack. At my current employer we might use node to set up a quick server for websockets or webhooks. ","node-js.png"),
    new Skill('Laravel',"Working with Rails has made working with Laravel extremely easy transition. Working with PHP has similar elements to Ruby as they are both object oriented.","laravel.svg"),
    new Skill('HTML5', 'HTML remains at the core of web development I do every day. .', 'html5.png'),
    new Skill('CSS3', 'HTML and CSS were some of the first languages I studied, as you would expect. I began teaching myself online, mainly through Lynda.com, Codeacademy.com, but desired to return to a classroom environment to accelerate my learning. Before I applied to a full-stack immersive class, I attended a front-end part-time course in Austin, TX at Makersquare. This was great initial exposure to HTML, CSS, Javascript, and CSS frameworks like Zurb Foundation to build responsive environments. HTML and CSS remain at the core of all my front end development.', 'css3.svg'),
    new Skill('jQuery',"jQuery is a technology that I still find myself using from time to time. Mostly I rely on jQuery for ajax calls, DOM manipulation and search, or existing third party plugins.","jquery.gif"),
    new Skill('AngularJS',"Angular is currently my preferred frontend framework. This site was designed with Angular 4. I have worked with both Angular 1 and 2/4. The power of the component design with Angular 2 is really compelling.","angularjs.png"),
    new Skill('PostgreSQL',"Most of the Rails appliations I have used have a Postgres or MySQL based database. I've become use to setting up postgres on servers and working with existing databases using psql.","postgres.svg"),
    new Skill('MongoDB',"Mongo is something that I frequently use with Node applications. When making apps in the M.E.A.N stack, Mongo with NoSQL is the way to go. I prefer SQL based databases, however.","mongodb.png"),
    new Skill('Sinatra',"If I want to spin up a quick web serve with Ruby, Sintra is the way to go. Most of the time I'm using Rails but this light frameworks has it's advantages.","sinatra.png"),
    new Skill('Swift',"Swift is preferred to me over objective-c when programming iOS. Working with Xcode, I have developed a few apps and worked on legacy code as well.","swift.png"),
    new Skill('Adobe Photoshop',"Photoshop remains a tool I use mostly for modifying page assets on the frontend. I use it sometimes for web mock ups as well.","photoshop.png"),
    new Skill('Adobe InDesign',"InDesign is great for creating marketing material or quick mock ups.","indesign.png"),
    new Skill('PHP',"PHP is used mainly with Laravel. In terms of server side languages, PHP is secondary to Ruby.","php.png")
  ];

  thirdParties: Array<Skill> = [
    new Skill('Stripe',"","stripe.png"),
    new Skill('CardFlight',"","cardflight2.png"),
    new Skill('Payeezy',"","payeezy.png"),
    new Skill('Heroku',"","heroku2.svg"),
    new Skill('Amazon Web Services',"","aws.png"),
    new Skill('Firebase',"","firebase.png"),
    new Skill('Atlassian',"","atlassian.png"),
    new Skill('Mixpanel',"","mixpanel.png"),
    new Skill('Twitter API',"","twitter.png"),
    new Skill('Facebook API',"","facebook.png"),
    new Skill('Codeship',"","codeship.png"),
    new Skill('Ionic',"","ionic.png"),
  ];

  constructor() { }

  ngOnInit() {
  }

  showSkill(skill) {
    this.skillDescription = skill.description;
    setTimeout(function(){ }, 150);
  }

}



