import { BrowserModule } from '@angular/platform-browser';
import { NgModule, Pipe, PipeTransform } from '@angular/core';
import { MaterializeModule } from 'angular2-materialize';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { SkillsComponent } from './skills/skills.component';
import { SkillCardComponent } from './skills/components/skill-card.component';
import { SkillBoxComponent } from './skills/components/skill-box.component';
import { ThirdPartyBoxComponent } from './skills/components/third-party-box.component';

import { AboutComponent } from './about/about.component';
import { BlogsComponent } from './blogs/blogs.component';
import { ResumeComponent } from './resume/resume.component';
import { ProjectsComponent } from './projects/projects.component';
import { ProjectCardComponent } from './projects/components/project-card.component';
import { FooterComponent } from './footer/footer.component';

@Pipe({ name: 'doubles' })
export class DoublePipe implements PipeTransform {
  transform(value:any) {
    var newArr = [];
    for (var i=0; i<value.length; i+=2) {
      newArr.push(value.slice(i, i+2));
    }
    return newArr;
  }
}

@Pipe({ name: 'triples' })
export class TriplePipe implements PipeTransform {
  transform(value:any) {
    var newArr = [];
    for (var i=0; i<value.length; i+=3) {
      newArr.push(value.slice(i, i+3));
    }
    return newArr;
  }
}

@Pipe({ name: 'each_slice' })
export class EachSlicePipe implements PipeTransform {
  transform(value:any, chunk:number) {
    var newArr = [];
    for (var i=0; i<value.length; i+=chunk) {
      newArr.push(value.slice(i, i+chunk));
    }
    return newArr;
  }
}

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    SkillsComponent,
    SkillCardComponent,
    SkillBoxComponent,
    ThirdPartyBoxComponent,
    AboutComponent,
    BlogsComponent,
    ResumeComponent,
    ProjectsComponent,
    ProjectCardComponent,
    TriplePipe,
    DoublePipe,
    EachSlicePipe,
    FooterComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    MaterializeModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



