import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-third-party-box',
  templateUrl: './third-party-box.component.html',
  styleUrls: ['./third-party-box.component.css']
})

export class ThirdPartyBoxComponent implements OnInit {

  @Input() title: string;
  @Input() description: string;
  @Input() imagePath: string;

  constructor() { }

  ngOnInit() {
    console.log("New card: ", this.title, this.description, this.imagePath);
  }

}



