import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-project-card',
  templateUrl: './project-card.component.html',
  styleUrls: ['./project-card.component.css']
})

export class ProjectCardComponent implements OnInit {
  rubyUsed: boolean;
  railsUsed: boolean;
  jsUsed: boolean;
  herokuUsed: boolean;
  awsUsed: boolean;
  angularUsed: boolean;
  postgresUsed: boolean;
  phaserUsed: boolean;
  famousUsed: boolean;
  nodeUsed: boolean;
  mongoUsed: boolean;
  swiftUsed: boolean;
  ionicUsed: boolean;
  laravelUsed: boolean;

  parsedTechUsed: Array<string>;

  @Input() title: string;
  @Input() description: string;
  @Input() imagePath: string;
  @Input() techUsed: string;
  @Input() publicLink: string;
  @Input() idTag: string;


  constructor() { }

  ngOnInit() {
    this.parsedTechUsed = this.techUsed.split(',');
    console.log("New card: ", this.title, this.description, this.techUsed, this.parsedTechUsed);

    if (this.parsedTechUsed.includes('Ruby on Rails')) { this.railsUsed = true; }

    if (this.parsedTechUsed.includes('JavaScript')) { this.jsUsed = true; }
    if (this.parsedTechUsed.includes('AngularJS')) { this.angularUsed = true; }
    if (this.parsedTechUsed.includes('Heroku')) { this.herokuUsed = true; }
    if (this.parsedTechUsed.includes('Amazon Web Services')) { this.awsUsed = true; }
    if (this.parsedTechUsed.includes('PostgreSQL')) { this.postgresUsed = true; }
    if (this.parsedTechUsed.includes('Phaser.io (Game engine)')) { this.phaserUsed = true; }
    if (this.parsedTechUsed.includes('Famo.us (Rebranded Since)')) { this.famousUsed = true; }
    if (this.parsedTechUsed.includes('Node.js')) { this.nodeUsed = true; }
    if (this.parsedTechUsed.includes('MongoDB')) { this.mongoUsed = true; }
    if (this.parsedTechUsed.includes('Swift')) { this.swiftUsed = true; }
    if (this.parsedTechUsed.includes('Ionic')) { this.ionicUsed = true; }
    if (this.parsedTechUsed.includes('Laravel')) { this.laravelUsed = true; }
  }

}



